import React, { useState, useEffect } from 'react';
import { getApartments } from "api/apartments";
import styled from 'styled-components';
import CatalogItemSceleton from "components/Catalog/CatalogItemSceleton";
import CatalogItem from "components/Catalog/CatalogItem";

const LastApartments = () => {
    const [apartments, setApartments] = useState(null);

    useEffect(() => {
        getApartments('', 9)
            .then(data => setApartments(data))
            .catch(e => setApartments(null))
    }, []);

    if (apartments) return (
        <Container>
            <Title>–û—Å—Ç–∞–Ω–Ω—ñ –ø—Ä–æ–ø–æ–∑–∏—Ü—ñ—ó</Title>
            {apartments.length ?
                apartments.map(el => <CatalogItem key={el.id} apartment={el}/>) :
                Array(9).fill(<CatalogItemSceleton />)
            }
        </Container>
    )
    return null;
}

export default LastApartments;

const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 1000px;
  margin: 50px auto;
  justify-content: center;
`;

const Title = styled.h2`
  text-align: center;
  width: 100%;
  font-size: 2.5rem;
  margin-bottom: 0;
`
