import React, { useState, useEffect } from 'react';
import styled from "styled-components";
import { getApartmentsCount } from "api/apartments";
import { Skeleton } from "primereact/skeleton";

const ApartmentsCount = () => {
    const [ count, setCount ] = useState('loading');
    useEffect(() => {
        getApartmentsCount().then(count => {
            setCount(count);
        }).catch((e) => {
            console.error(e);
            setCount('error');
        });
    });

    if (count === 'loading') return (<TitleSkeleton/>);
    else if (count) return (
        <Title>–í —Å–µ—Ä–≤—ñ—Å—ñ –∑–∞—Ä–∞–∑ {count} –ø–æ–º–µ—à–∫–∞–Ω—å üè†</Title>
    );
    return  null;
};

export default ApartmentsCount;

const Title = styled.h1`
  text-align: center;
  width: max-content;
  max-width: 100%;
  font-size: 3rem;
  margin: 40px auto;
`

const TitleSkeleton = styled(Skeleton)`
  width: 90% !important;
  max-width: 600px;
  height: 3rem !important;
  margin: 40px auto;
`;